<template>
    <yd-layout>
        <div class="mainContent">
            <div class="contentSradius holiday">
                <div class="holiday_one">
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="hint">
                                    我的请假申请
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="waitApprove">
                                    {{judgmentState(state)}}
                                </div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
                <div class="holiday_two formData">
                    <i class="circle01"></i>
                    <i class="circle02"></i>
                    <yd-flexbox direction="vertical">
                        <yd-flexbox-item>
                            <yd-flexbox>
                                <div class="leftWord">
                                    所在科室：
                                </div>
                                <div class="rightWord" v-text="historyDatiel.firstorgname"></div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="leftWord">
                                    请假类型：
                                </div>
                                <div class="rightWord" v-text="holidayType[historyDatiel.leavetype-1]"></div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="leftWord">
                                    开始时间：
                                </div>
                                <div class="rightWord">{{formatDate(historyDatiel.startDate,"yyyy-MM-dd")}}</div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="leftWord">
                                    结束时间：
                                </div>
                                <div class="rightWord">{{formatDate(historyDatiel.endDate, "yyyy-MM-dd")}}</div>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="leftWord">
                                    时长(天) ：
                                </div>
                                <div class="rightWord">{{historyDatiel.days}}天</div>
                            </yd-flexbox>
                            <yd-flexbox v-if="historyDatiel.isOut!=''&&(historyDatiel.applyLevel=='2'||historyDatiel.orgname=='总经理室')">
                                <div class="leftWord">
                                   出国（境）：
                                </div>
                                <div class="rightWord" v-if="historyDatiel.isOut=='1'">是</div>
                                <div class="rightWord" v-if="historyDatiel.isOut=='0'">否</div>
                            </yd-flexbox>
                            <yd-flexbox v-if="historyDatiel.outDate!=''&&(historyDatiel.applyLevel=='2'||historyDatiel.orgname=='总经理室')">
                                <div class="leftWord">
                                   离境时间：
                                </div>
                                <div class="rightWord">{{historyDatiel.outDate}}</div>
                            </yd-flexbox>
                            <yd-flexbox v-if="historyDatiel.enterDate!=''&&(historyDatiel.applyLevel=='2'||historyDatiel.orgname=='总经理室')">
                                <div class="leftWord">
                                   入境时间：
                                </div>
                                <div class="rightWord">{{historyDatiel.enterDate}}</div>
                            </yd-flexbox>
                            <yd-flexbox v-if="historyDatiel.agentname!=''">
                                <div class="leftWord">
                                    工作代理人：
                                </div>
                                <div class="rightWord" v-text="historyDatiel.agentname"></div>
                            </yd-flexbox>
                            <yd-flexbox v-if="historyDatiel.address!=''&&(historyDatiel.applyLevel=='1'||historyDatiel.applyLevel=='2'||historyDatiel.orgname=='总经理室')">
                                <div class="leftWord">
                                    请假地点：
                                </div>
                                <div class="rightWord" v-text="historyDatiel.address"></div>
                            </yd-flexbox>
                            <yd-flexbox v-if="historyDatiel.remark!=''">
                                <div class="leftWord remarkLeft">
                                    请假事由：
                                </div>
                                <div class="rightWord remarkRight" v-html="historyDatiel.remark"></div>
                            </yd-flexbox>
                        </yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
        </div>
        <div class="mainContentFluid">
            <div class="historyWidth">
                <div class="approHistory">
                    <div class="title">
                        审批记录
                    </div>
                    <div class="noRecord" v-show="approProcess.length==0">暂无审批记录</div>
                    <div v-for="(item, index) in approProcess" :key="index" v-if="approProcess.length>0">
                        <yd-flexbox>
                            <div class="approName" v-if="item.activityDefName!='领导秘书预审'">{{item.partiname}}：</div>
                            <yd-flexbox-item class="neirongSize" v-html="transferString(item.neirong)"></yd-flexbox-item>
                        </yd-flexbox>
                        <yd-flexbox>
                            <div class="approTime">{{formatDate(item.submitDate, 'yyyy-MM-dd')}}</div>
                            <div class="approTime">{{formatDate(item.submitDate, 'hh-mm')}}</div>
                        </yd-flexbox>
                    </div>
                </div>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'myApplication',
    data() {
        return {
            approProcess: [],
            historyDatiel: '',
            holidayType: ['年休假', '病假', '事假', '婚假', '产前假', '产假', '工伤假'],
            applicanttime: '',
            workId: this.$route.params.workId,
            state: this.$route.params.state
        }
    },
    mounted() {
        document.setTitle('请休假');
        this.holidayData();
        this.historyApprove();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        // 基本信息
        holidayData() {
            var vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                'type': 'askforleave',
                'workItemID': vm.workId,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'random': Math.floor(Math.random() * (100000 + 1))
            }, function (res) {
                vm.historyDatiel = res.jsonData;
                vm.applicanttime = res.jsonData.applicanttime;
            });
        },
        // 审批信息
        historyApprove() {
            var vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryApprove', {
                'type': 'askforleave',
                'workItemID': vm.workId,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'random': Math.floor(Math.random() * (100000 + 1))
            }, function (res) {
                if (res.jsonData != '') {
                    vm.approProcess = res.jsonData;
                }
            });
        },
        judgmentState(state) {
            if (state == '0') {
                return '进行中';
            } else {
                return '已结束';
            }
        }
    }
}
</script>
<style scoped>
.mainContentFluid {
    padding-bottom: 1rem;
}

.holiday_two {
    border-bottom: none;
    position: relative;
}

.agree {
    color: #35a4ff;
}

.holiday_one {
    border-bottom: 1px dashed #eaeaea;
}

.circle01 {
    height: .2rem;
    width: .2rem;
    background-color: #eaeaea;
    position: absolute;
    top: -0.1rem;
    left: -0.25rem;
    border-radius: 50%;
}

.circle02 {
    height: .2rem;
    width: .2rem;
    background-color: #eaeaea;
    position: absolute;
    top: -0.1rem;
    right: -0.25rem;
    border-radius: 50%;
}

.mainContent {
    height: auto;
}

.noRecord {
    font-size: .28rem;
    padding: 0 .2rem;
    height: .5rem;
    line-height: .5rem;
    color: #666;
}

.remarkLeft {
    position: absolute;
    top: 0;
    left: 0;
}

.remarkRight {
    width: 6rem;
    overflow: auto;
    word-break: break-all;
    outline: none;
    user-select: text;
    white-space: pre-wrap;
    text-align: left;
    margin-left: 1.6rem;
}

.approName, .approTime, .neirongSize {
    font-size: .32rem!important;
}
</style>


